import { manifests as propertyeditors } from './property-editor-ui/manifests.ts';
import { manifests as dialogs } from './dialogs/manifests.ts'
import { UmbEntryPointOnInit } from '@umbraco-cms/backoffice/extension-api';

import type { ManifestModal, ManifestPropertyEditorUi } from '@umbraco-cms/backoffice/extension-registry';

const manifests: Array<ManifestPropertyEditorUi | ManifestModal> = [
	...propertyeditors,
	...dialogs
];

export const onInit: UmbEntryPointOnInit = (_host, extensionRegistry) => {
	extensionRegistry.registerMany(manifests)
};